.CardDisplayArea {
  background-color: white;
}

.CardQuestion {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 6vw;
  /* //2rem; */
  /* height: 50vh; */
}
.CardAnswer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4vw;
  /* height: 50vh; */
}
@media screen and (min-width: 600px) {
  .CardQuestion {
    font-size: 36px;
  }
  .CardAnswer {
    font-size: 24px;
  }
}
/* this is to display \n in card questoins and answers */
.display-linebreak {
  white-space: pre-line;
}
